'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';

export default function WakaTimeStats() {
  const [stats, setStats] = useState({
    totalTime: '0',
    dailyAverage: '0',
    languages: []
  });

  useEffect(() => {
    // 模拟获取数据
    setStats({
      totalTime: '23.5',
      dailyAverage: '3.2',
      languages: [
        { name: 'HTML', percentage: 40 },
        { name: 'CSS', percentage: 35 },
        { name: 'JavaScript', percentage: 25 }
      ]
    });
  }, []);

  return (
    <div className="max-w-4xl mx-auto py-8 px-4">
      <div className="text-center mb-8">
        <div className="relative w-24 h-24 mx-auto mb-4">
          <Image
            src="/practice/images/樱桃小丸子图片生成 (12).png"
            alt="Maruko"
            fill
            className="rounded-full object-cover"
          />
        </div>
        <h2 className="text-2xl font-bold text-[#FF6B6B] mb-2">小丸子的学习报告</h2>
        <p className="text-gray-600">让我们看看今天学习了多久吧！</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="maruko-card p-6">
          <h3 className="text-lg font-bold text-[#FF6B6B] mb-4">今日学习时间</h3>
          <div className="flex items-center justify-center">
            <div className="text-4xl font-bold text-[#FF6B6B]">{stats.totalTime}</div>
            <div className="ml-2 text-gray-600">小时</div>
          </div>
          <div className="mt-4 text-center text-gray-600">
            平均每天学习 {stats.dailyAverage} 小时
          </div>
        </div>

        <div className="maruko-card p-6">
          <h3 className="text-lg font-bold text-[#FF6B6B] mb-4">学习内容分布</h3>
          <div className="space-y-4">
            {stats.languages.map((lang, index) => (
              <div key={lang.name}>
                <div className="flex justify-between text-sm mb-1">
                  <span className="text-gray-600">{lang.name}</span>
                  <span className="text-gray-600">{lang.percentage}%</span>
                </div>
                <div className="w-full h-2 bg-gray-200 rounded-full">
                  <div
                    className="h-full rounded-full"
                    style={{
                      width: `${lang.percentage}%`,
                      backgroundColor: index === 0 ? '#FF9999' : index === 1 ? '#FFE066' : '#66B3FF'
                    }}
                  ></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="maruko-card p-6 mt-6">
        <div className="flex items-center">
          <Image
            src="/practice/images/樱桃小丸子图片生成 (13).png"
            alt="Tip"
            width={60}
            height={60}
            className="rounded-full mr-4"
          />
          <div>
            <h3 className="text-lg font-bold text-[#FF6B6B] mb-2">小丸子说：</h3>
            <p className="text-gray-600">
              今天的学习很棒哦！记得保持这个学习热情，明天继续加油！
            </p>
          </div>
        </div>
      </div>
    </div>
  );
} 